import React from 'react'
import {Modal} from 'antd'

const AvatarModal = ({isAvatarModalVisible, setIsAvatarModalVisble, avatars, handleAvatarChange})=>{

    return(
        <>
        <Modal visible={isAvatarModalVisible} footer={false} centered onCancel={()=>setIsAvatarModalVisble(false)} title={'Avatars!!'}>
            <div className="row">
                {avatars && avatars.map((item)=>{
                    return (
                        <>
                            <div className="col-6 col-lg-4 mb-4">
                                <img src={item} className="img-fluid" onClick={()=>handleAvatarChange(item)}/>
                            </div>
                        </>
                    )
                })}
            </div>
        </Modal>
        </>
    )
}

export default AvatarModal