import React,{useState, useEffect} from 'react'
import Feed from '../../components/Feed'
import {useSelector} from 'react-redux'
import { getUserMessage, removeMessage } from '../../functions/message'

const SendMessages = ()=>{
    const [posts, setPosts] = useState('')
    const {user} = useSelector((state)=>({...state}))
    const loadAllMessages = ()=>{
        getUserMessage(user && user.token).then((res)=>{
            setPosts(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    useEffect(()=>{
        loadAllMessages()
    },[user])

    const handleMessageDelete=(id)=>{
        removeMessage(user && user.token, id).then((res)=>{
            loadAllMessages()
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
            <Feed loadAllMessages={loadAllMessages} heading={'Send Messages'} bgColor={'#2ea8ff'} iconBgColor={'#2ea8ff'} posts={posts} handleMessageDelete={handleMessageDelete} sentTo={true}/>
        </>
    )
}

export default SendMessages