import React, {useEffect} from 'react'
import {Switch, Route, useHistory} from 'react-router-dom'
import {useDispatch, useSelector, useState} from 'react-redux'
import { auth } from './firebase'
import { currentUser } from './functions/auth'
import Login from './pages/auth/Login'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';

import Register from './pages/auth/Register'
import RegisterComplete from './pages/auth/RegisterComplete'
import Home from './pages/universal/Home'
import Profile from './pages/universal/Profile'
import Messages from './pages/universal/Meesages'
import LikedMessages from './pages/universal/LikedMessages'
import SendMessages from './pages/universal/SendMessages'
import EditProfile from './pages/universal/EditProfile'
import Search from './pages/universal/Search'
import UserProfile from './pages/universal/UserProfile'
import FriendRequest from './pages/universal/FriendRequest'
import Friends from './pages/universal/Friends'

const App = ()=>{
    const history = useHistory()
    const {user} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()
    useEffect(()=>{
        auth.onAuthStateChanged(async(user)=>{
            if(user){
                let idToken = await user.getIdTokenResult()
                let user_token = idToken.token
                currentUser(user_token).then((res)=>{
                    const {email, username, info, _id} = res.data
                    dispatch({
                        type:'LOGGED_IN_USER',
                        payload:{
                            id:_id,
                            email,
                            username,
                            info,
                            token:user_token
                        }
                    })
                    history.push('/messages')
                }).catch((err)=>{
                    console.log(err)
                })
            }
        })
    },[])

    const rednerRoutes = ()=>{
        if(user){
            return(
                <>
                    <Route exact path='/messages' component={Messages}></Route>
                    <Route exact path='/messages/liked' component={LikedMessages}></Route>
                    <Route exact path='/messages/send' component={SendMessages}></Route>
                    <Route exact path='/user/:username' component={Profile}></Route>
                    <Route exact path='/user/:username/profile' component={UserProfile}></Route>
                    <Route exact path='/user/:username/edit' component={EditProfile}></Route>
                    <Route exact path='/friend/requests' component={FriendRequest}></Route>
                    <Route exact path='/user/friends/all' component={Friends}></Route>
                </>
            )
        }else{
            return(
            <>
                <Route exact path='/' component={Home}></Route>
                <Route exact path='/register' component={Register}></Route>
                <Route exact path='/register/complete' component={RegisterComplete}></Route>
                <Route exact path='/login' component={Login}></Route>
            </>
            )
        }
    }
    return(
        <>
            <ToastContainer/>
            <Switch>
                <Route exact path='/search' component={Search}></Route>
                {rednerRoutes()}
            </Switch>
        </>
    )
}

export default App