import React, {useState, useEffect} from 'react'
import Navbar from '../../components/Navbar'
import { getUserInfo } from '../../functions/user'
import {useSelector, useDispatch} from 'react-redux'
import './Profile.css'
import firebase from 'firebase'
import { getUserMessage } from '../../functions/message'
import GifModal from '../../components/GifModal'
import SideNav from '../../components/SideNav'
import {Link} from 'react-router-dom'

const Profile=({match, history})=>{
    const [isGifVisible, setIsGifVisible] = useState(false)
    const [isDrawerVisible, setIsDrawerVisible] = useState(false)
    const [gif, setGif] = useState('')
    const [data, setData] = useState('')
    const dispatch = useDispatch()
    const {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        getUserInfo(user && user.token, user.username).then((res)=>{
            setData(res.data)
        }).catch((err)=>{
            console.log(err)
        })


        //TEST
        getUserMessage(user && user.token).then((res)=>{
            // console.log(res.data)
        })
    },[match.params.username])

    const handleLogout=async()=>{
        await firebase.auth().signOut()
        dispatch({
            type:'LOG_OUT_USER',
            payload:null
        })
        history.push('/')
    }
    return(
        <>
        
        <section className="profile pb-4 pt-0">
                <div className="profile_bg" style={{backgroundImage:`url(${data && data.banner ? data.banner : 'https://i.pinimg.com/originals/23/4e/bd/234ebdb769596d09a2d2d3fc1bce8293.jpg'})`}}>
                    <div className="user_profile_pic">
                        <img src={data && data.avatar ? data.avatar : 'https://therminic2018.eu/wp-content/uploads/2018/07/dummy-avatar.jpg'} className="img-fluid"/>
                    </div>
                    <i className="fa fa-bars setting_btn" onClick={()=>{setIsDrawerVisible(true)}}></i>
                </div>
            <div className="container py-3 mb-4">
                <div className="row">
                    <div className="col-11 col-lg-8 mx-auto">
                        {data && (<>
                            <p>{data.info.firstName} {data.info.lastName}</p>
                            <p>{data.username}</p>
                            <p>{data.info.bio ? data.info.bio : <Link to={`/user/${data.username}/edit`}>Edit Your Profile!!</Link>}</p>
                        </>)}
                    </div>
                </div>
            </div>
        </section>
        <GifModal isGifVisible={isGifVisible} setIsGifVisible={setIsGifVisible} setGif={setGif} gif={gif} handleLogout={handleLogout} logout={true}/>
        <SideNav isDrawerVisible={isDrawerVisible} setIsDrawerVisible={setIsDrawerVisible} setGif={setGif} setIsGifVisible={setIsGifVisible} username={data && data.username}/>
        <Navbar/>
        </>
    )
}

export default Profile