import React,{useState, useEffect} from 'react'
import Navbar from '../../components/Navbar'
import {useSelector} from 'react-redux'
import { getAllFriends, removeFriend } from '../../functions/user'
import { Avatar } from '@material-ui/core'
import './Friends.css'

const Friends = ({history})=>{
    const {user} = useSelector((state)=>({...state}))
    const [data, setData] = useState([])

    const loadAllFriends = ()=>{
        getAllFriends(user && user.token).then((res)=>{
            setData(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }


    useEffect(()=>{
        loadAllFriends()
    },[user])

    const handleUnfriend = (user_id)=>{
        removeFriend(user && user.token,user_id).then((res)=>{
            if(res.data.success){
                loadAllFriends()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    return(
        <>
        <section className="friend_request pb-5 pt-4">
                <h2 className="text-center text-uppercase">Friends</h2>
                <hr/>
                <div className="container py-3">
                    <div className="row">
                        {data && data.length > 0 ? data.map((item)=>{
                            return (
                                <>
                                    <div className="col-12  mx-auto flex-wrap justify-content-between mb-4 friend_card d-flex align-items-center">
                                        <Avatar src={item.avatar} style={{height:'80px', width:'80px'}}/>
                                        <div className="ml-3 friend_info_cont">
                                            <p style={{cursor:'pointer'}} onClick={()=>history.push(`/user/${item.username}/profile`)}>{item.info.firstName} {item.info.lastName}</p>
                                            <p>{item.username}</p>
                                        </div>   
                                        <button className="btn btn-sm btn-outline-danger" onClick={()=>handleUnfriend(item._id)}>Unfriend</button>
                                    </div>
                                </>
                            )
                        }) : (
                            <>
                                <div className="no_friend_cont col-11 mx-auto text-center">
                                    <img src={user && user.info.gender === 'Male' ? 'https://i.gifer.com/VmEH.gif' :'https://i.pinimg.com/originals/ac/47/8a/ac478a74b213f6ed04a5a036dd479397.gif'} className="img-fluid"/>
                                    <p className="head">No Friend Requests</p>
                                    <p className="desc">You might have not told your friend about contact...Tell your friends!!</p>
                                    <button className="btn-grad" onClick={()=>history.push('/search')}>Search Your Friend</button>
                                </div>
                            </>
                        )}
                        
                    </div>
                </div>

            </section>
        <Navbar/>
        </>
    )
}

export default Friends