import React, {useState, useEffect} from 'react'
import Navbar from '../../components/Navbar'
import {useSelector} from 'react-redux'
import { acceptFriendRequest, getRequests, removeMainRequest } from '../../functions/user'
import { Avatar, Badge } from '@material-ui/core'
import { CloseOutlined, CheckOutlined } from '@material-ui/icons'

const FriendRequest = ({history})=>{
    const {user} = useSelector((state)=>({...state}))
    const [data, setData] = useState([])

    const loadRequests = ()=>{
        getRequests(user && user.token).then((res)=>{
            setData(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    useEffect(()=>{
        loadRequests()
    }, [user])

    const handleReject = (user_id)=>{
        removeMainRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                loadRequests()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleAccept = (user_id)=>{
        acceptFriendRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                loadRequests()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
            <section className="friend_request pb-5 pt-4">
                <h2 className="text-center text-uppercase">Friend Requests</h2>
                <hr/>
                <div className="container py-3">
                    <div className="row">
                        {data && data.length > 0 ? data.map((item)=>{
                            return (
                                <>
                                    <div className="col-12 mx-auto friend_card d-flex justify-content-between align-items-center">
                                        <Avatar src={item.avatar} style={{height:'80px', width:'80px'}}/>
                                        <div>
                                            <p>{item.info.firstName} {item.info.lastName}</p>
                                            <p>{item.username}</p>
                                        </div>   
                                        <div>
                                            <button className="btn btn-primary btn-sm mr-2" onClick={()=>handleAccept(item._id)}><CheckOutlined/></button>
                                            <button className="btn btn-danger btn-sm" onClick={()=>handleReject(item._id)}><CloseOutlined/></button>
                                        </div>
                                    </div>
                                </>
                            )
                        }) : (
                            <>
                            <div className="no_friend_cont col-11 mx-auto text-center">
                                <img src={user && user.info.gender === 'Male' ? 'https://i.gifer.com/VmEH.gif' :'https://i.pinimg.com/originals/ac/47/8a/ac478a74b213f6ed04a5a036dd479397.gif'} className="img-fluid"/>
                                <p className="head">No Friend Requests</p>
                                <p className="desc">You might have not told your friend about contact...Tell your friends!!</p>
                                <button className="btn-grad" onClick={()=>history.push('/search')}>Search Your Friend</button>
                            </div>
                            </>
                        )}
                        
                    </div>
                </div>

            </section>
            <Navbar/>
        </>
    )
}

export default FriendRequest