import React, {useState, useEffect} from 'react'
import Navbar from '../../components/Navbar'
import { getUserInfo, updateUserAvatar, updateUserProfile, updateUserBanner } from '../../functions/user'
import './EditProfile.css'
import {useSelector, useDispatch} from 'react-redux'
import AvatarModal from '../../components/AvatarModal'
import { boysAvatar, girlsAvatar, bannerPic } from './images'
import BannerModal from '../../components/BannerModal'
import {LoadingOutlined} from '@ant-design/icons'

const EditProfile = ({history})=>{
    const initialState = {
        firstName:'',
        lastName:'',
        dob:'',
        bio:'',
        gender:''
    }
    const [email, setEmail] = useState('')
    const [username, setUsername] = useState('')
    const [avatar, setAvatar] = useState('')
    const [banner, setBanner] = useState('')
    const [info, setInfo] = useState(initialState)
    const [isAvatarModalVisible, setIsAvatarModalVisble] = useState(false)
    const [isBannerModalVisible, setIsBannerModalVisble] = useState(false)
    const [loading, setLoading] = useState(false)
    const {user} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()

    const loadProfile = ()=>{
        getUserInfo(user && user.token, user.username).then((res)=>{
            setUsername(res.data.username)
            setBanner(res.data.banner)
            setEmail(res.data.email)
            setAvatar(res.data.avatar)
            setInfo({...info, dob : res.data.info.dob.split('T')[0], firstName:res.data.info.firstName, lastName:res.data.info.lastName, bio:res.data.info.bio, gender:res.data.info.gender})
        }).catch((err)=>{
            console.log(err)
        })
    }
    
    useEffect(()=>{
        loadProfile()
    }, [user])

    const handleAvatarClick = ()=>{
        setIsAvatarModalVisble(true)
    }

    const handleAvatarChange = (item)=>{
        updateUserAvatar(user && user.token, user && user.username, item).then((res)=>{
            if(res.data.success){
                setIsAvatarModalVisble(false)
                loadProfile()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleBannerClick = ()=>{
        setIsBannerModalVisble(true)
    }

    const handleBannerChange = (item)=>{
        updateUserBanner(user && user.token, user && user.username, item).then((res)=>{
            if(res.data.success){
                setIsBannerModalVisble(false)
                loadProfile()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleInfoChange = (e)=>{
        setInfo({...info, [e.target.name]:e.target.value})
    }

    const handleSubmit = ()=>{
        setLoading(true)
        updateUserProfile(user && user.token, username, info).then((res)=>{
            if(res){
                let {info, username, email, _id} = res.data
                dispatch({
                    type:'LOGGED_IN_USER',
                    payload:{
                        id:_id,
                        email,
                        info,
                        username,
                        token:user && user.token
                    }
                })
                setLoading(false)
                history.push(`/user/${user && username}`)
            }
        }).catch((err)=>{
            console.log(err)
            setLoading(false)
        })
    }
    return(
        <>
        <section className=" mb-5 pb-4 edit_profile">
            <div className="profile_bg" style={{backgroundImage:`url(${banner})`}}>
                <div className="user_profile_pic">
                    <img src={avatar ? avatar : 'https://therminic2018.eu/wp-content/uploads/2018/07/dummy-avatar.jpg'} onClick={handleAvatarClick} className="img-fluid"/>
                </div>
                <i className="fa fa-pencil" onClick={handleBannerClick}></i>
            </div>
            <div className="container">
                <div className="row">
                    <div className="col-11 col-lg-7 mx-auto">
                        <input type='text' placeholder='First Name' value={info.firstName} name='firstName' onChange={handleInfoChange}/>
                        <input type='text' placeholder='Last Name' value={info.lastName} name='lastName' onChange={handleInfoChange}/>
                        <input type='text' placeholder='UserNAme' value={username} name='' onChange={(e)=>{setUsername(e.target.value)}}/>
                        <input type='text' value={email} disabled onChange={(e)=>setEmail(e.target.value)} placeholder="Email"/>
                        <input type='date' placeholder='Date Of Birth' value={info.dob} name='dob' onChange={handleInfoChange}/>
                        <textarea type='text' placeholder='Bio' value={info.bio} name='bio' onChange={handleInfoChange}></textarea><br/>
                        <select name="gender" value={info.gender} onChange={handleInfoChange}>
                            <option>Gender</option>
                            <option value="Male">Male</option>
                            <option value="Female">Female</option>
                        </select>
                        <button className="btn btn-primary mt-3 mb-5" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
                    </div>
                </div>
            </div>
        </section>
        <AvatarModal isAvatarModalVisible={isAvatarModalVisible} handleAvatarChange={handleAvatarChange} avatars={user && user.info.gender == 'Male' ? boysAvatar : girlsAvatar} setIsAvatarModalVisble={setIsAvatarModalVisble}/>
        <BannerModal isBannerModalVisible={isBannerModalVisible} setIsBannerModalVisble={setIsBannerModalVisble} banners={bannerPic && bannerPic} handleBannerChange={handleBannerChange}/>
        <Navbar/>
        </>
    )
}

export default EditProfile