import React, {useState} from 'react'
import { auth } from '../../firebase'
import {toast} from 'react-toastify'
import { LoadingOutlined } from '@ant-design/icons';

const Register = ()=>{
    const [email, setEmail] = useState('')
    const [loading, setLoading] = useState(false)

    const handleSubmit = async()=>{
        setLoading(true)
        let config={
            url:'https://kuch-kahna-tha.herokuapp.com/register/complete',
            handleCodeInApp:true
        }
       return await auth.sendSignInLinkToEmail(email, config).then((res)=>{
           setLoading(false)
            localStorage.setItem('emailForRegistration', email)
            toast.success('Email has been successfullt sent to' + email + '...Please Check!!')
            setEmail('')
        }).catch((err)=>{
            console.log(err)
            setLoading(false)
            toast.error('Some error occured...Please Try Again!!')
        })
    }
    return(
        <>  
        <section className="register">
            <div className="login_logo text-center mb-5">
                <p>Kuch Kahna Tha!!</p>
            </div>
            <div className="container my-5">
                <div className="row">
                    <div className="col-11 col-lg-6 mx-auto">
                        <p className="log_head mb-5">Sign Up into KKT</p>
                        <input type='text' value={email} onChange={(e)=>setEmail(e.target.value)} placeholder="Email"/>
                        <button className="register_btn" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
                    </div>
                </div>
            </div>
        </section>
        </>
    )
}

export default Register