import React, {useState} from 'react'
import { auth } from '../../firebase'
import './Login.css'
import {useDispatch} from 'react-redux'
import {Link} from 'react-router-dom'
import { createOrUpdateUser } from '../../functions/auth'
import ForgetPassModal from '../../components/ForgetPassModal'
import { LoadingOutlined } from '@ant-design/icons'
import { toast } from 'react-toastify'

const Login = ({history})=>{
    const [email, setEmail] = useState('')
    const [password ,setPassword] = useState('')
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [loading, setLoading] = useState(false)
    const dispatch = useDispatch()

    const handleSubmit=async()=>{
        setLoading(true)
        await auth.signInWithEmailAndPassword(email, password).then(async(res)=>{
            if(res.user.emailVerified){
                let idToken = await res.user.getIdTokenResult()
                let user_token = idToken.token
                createOrUpdateUser(user_token).then((result)=>{
                    const {email, username, info, _id} = result.data
                    dispatch({
                        type:'LOGGED_IN_USER',
                        payload:{
                            id:_id,
                            email,
                            username,
                            info,
                            token:user_token
                        }
                    })
                    history.push('/messages')
                    setLoading(false)
                })
            }
        }).catch((err)=>{
            toast.error('Invalid Email or Password....Please check and try again!!')
            setLoading(false)
        })
    }
    return(
        <>
        <section className="login">
            <div className="login_logo text-center mb-5">
                <p>Kuch Kahna Tha!!</p>
            </div>
            <div className="main_login_sec">
                <div className="container my-5">
                    <div className="row">
                        <div className="col-11 col-lg-6 mx-auto">
                            <p className="log_head mb-5">log into KKT</p>
                            <input type='text' value={email} onChange={(e)=>setEmail(e.target.value)} placeholder="Email"/>
                            <input type='password' value={password} onChange={(e)=>setPassword(e.target.value)} placeholder="Password"/><br/>
                            <button className="login_btn mt-3 mb-3" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit' }</button>
                            <p className="text-center"><Link to='/register'>Create Account</Link> / <Link onClick={()=>setIsModalVisible(true)}>Forgot Password</Link></p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <ForgetPassModal isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible}/>
        </>
    )
}

export default Login