import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import Feed from '../../components/Feed'
import GuessModal from '../../components/GuessModal'
import { getMessage, readMessage, updateGuess } from '../../functions/message'
import { addToLikedMessages } from '../../functions/user'
import { images } from './images'

const Messages = ()=>{
    const [posts, setPosts] = useState('')
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [name, setName] = useState('')
    const [answer, setAnswer] = useState('')
    const [modal_id, setModal_Id] = useState('')
    const [imgSrc, setImgSrc] = useState('https://media0.giphy.com/media/3ohhwEVKBOZd7bCQ7u/giphy.gif')
    const [loading, setLoading] = useState(false)
    let [count, setCount] = useState(0)
    const {user} = useSelector((state)=>({...state}))

    const loadAllMessages=()=>{
        getMessage(user && user.token).then((res)=>{
            setPosts(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    useEffect(()=>{
       loadAllMessages()
    },[user])

    const handleClick = (messageId)=>{
        addToLikedMessages(user && user.token, messageId).then((res)=>{
            loadAllMessages()
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleGuessClick =(id)=>{
        setModal_Id(id)
        readMessage(user && user.token, id).then((res)=>{
            setAnswer(res.data.postedBy.info.firstName)
            setIsModalVisible(true)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleModalSubmit=()=>{
        if(name){
            setLoading(true)
            if(name.toLowerCase() === answer.toLowerCase()){
                updateGuess(user && user.token, modal_id, true).then((res)=>{
                    setImgSrc('https://www.memestemplates.com/wp-content/uploads/2020/08/bahot-hi-umda-kheli-aap-taaliyan-bajti-rehni-chahiye.jpg')
                    setLoading(false)
                    setTimeout(()=>{
                        setIsModalVisible(false)
                        loadAllMessages()
                        setImgSrc('https://media0.giphy.com/media/3ohhwEVKBOZd7bCQ7u/giphy.gif')
                        setAnswer('')
                        setName('')
                        setCount(0)
                    }, 3000)
                    
                }).catch((err)=>{
                    console.log(err)
                    setLoading(false)
                })
                
            }else{
                setLoading(false)
                document.querySelector('.guess_input').style.borderBottom = '0.1rem solid red'
                document.querySelector('.error_msg').classList.remove('d-none')
                document.querySelector('.error_msg_text').innerText = 'Your Guess was incorrect!!'
                setCount(++count)
                let num = Math.floor(Math.random() * images.length)
                if(count > 0){
                    setImgSrc(images[num])
                }
            }
        }else{
            setLoading(false)
            setImgSrc('https://media0.giphy.com/media/3ohhwEVKBOZd7bCQ7u/giphy.gif')
            document.querySelector('.guess_input').style.borderBottom = '0.1rem solid red'
            document.querySelector('.error_msg').classList.remove('d-none')
            document.querySelector('.error_msg_text').innerText = 'Please Type A Name!'
        }
    }

    const onModalClose = ()=>{
        setIsModalVisible(false)
        setImgSrc('https://media0.giphy.com/media/3ohhwEVKBOZd7bCQ7u/giphy.gif')
        setName('')
        setAnswer('')
        setCount(0)
        document.querySelector('.guess_input').style.borderBottom = '0.1rem solid rgb(182, 182, 182)'
        document.querySelector('.error_msg').classList.add('d-none')
    }
    return(
        <>
            <Feed handleGuessClick={handleGuessClick} loadAllMessages={loadAllMessages} heading={'Messages'} bgColor={'#06ba00'} iconBgColor={'#06ba00'} posts={posts && posts} handleClick={handleClick}/>
            <GuessModal loading={loading} setImgSrc={setImgSrc} count={count} onModalClose={onModalClose} imgSrc={imgSrc} handleModalSubmit={handleModalSubmit} name={name} setName={setName} isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible} answer={answer}/>
        </>
    )
}

export default Messages