import React, {useState, useEffect} from 'react'
import MessageModal from '../../components/MessageModal'
import { getMessagePostedTo } from '../../functions/message'
import { getUserProfile, postRequest,getMutualFriends, removeFriend, removeRequest, getUserInfo, acceptFriendRequest, removeMainRequest } from '../../functions/user'
import {useSelector} from 'react-redux'
import Navbar from '../../components/Navbar'
import './UserProfile.css'
import { LoadingOutlined } from '@ant-design/icons'
import MutualModal from '../../components/MutualModal'

const UserProfile = ({match, history})=>{
    const {user} = useSelector((state)=>({...state}))
    const [messages, setMessages] = useState([])
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [data, setData] = useState('')
    const [mutual, setMutual] = useState([])
    const [mainUser, setMainUser] = useState('')
    const [requestLoading, setRequestLoading] = useState(false)
    const [accpetLoading, setAcceptLoading] = useState(false)
    const [rejectLoading, setRejectLoading] = useState(false)
    const [isMutualModalVisible, setIsMutualModalVisible] = useState(false)

    const loadProfile = ()=>{
        getUserProfile(match.params.username).then((res)=>{
            setData(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const getAllMutualFriends = (user_id)=>{
        getMutualFriends(user && user.token, user_id).then((res)=>{
            setMutual(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    useEffect(()=>{
        loadProfile()
    },[match.params.username])

    useEffect(()=>{
        getAllMutualFriends(data && data._id)
    },[data])

    useEffect(()=>{
        getUserInfo(user && user.token, user.username).then((res)=>{
            setMainUser(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }, [user])

    const sendRequest = (user_id)=>{
        setRequestLoading(true)
        postRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                setRequestLoading(false)
                loadProfile()
            }
        }).catch((err)=>{
            setRequestLoading(false)
            console.log(err)
        })
    }

    const removeFriendRequest = (user_id)=>{
        removeRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                loadProfile()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }   
    
    const handleUnfriend=(user_id)=>{
        removeFriend(user && user.token,user_id).then((res)=>{
            if(res.data.success){
                loadProfile()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleReject = (user_id)=>{
        setRejectLoading(true)
        removeMainRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                loadProfile()
                setRejectLoading(false)
            }
        }).catch((err)=>{
            setRejectLoading(false)
            console.log(err)
        })
    }

    const handleAccept = (user_id)=>{
        setAcceptLoading(true)
        acceptFriendRequest(user && user.token, user_id).then((res)=>{
            if(res.data.success){
                setAcceptLoading(false)
                loadProfile()
                getAllMutualFriends(user_id)
            }
        }).catch((err)=>{
            setAcceptLoading(false)
            console.log(err)
        })
    }

    const renderMutual = ()=>{
        if(mutual && mutual.length > 0){
            for(let i = 0; i<6; i++){
                return(
                    <>
                    <div className="col-4" onClick={()=>history.push(`/user/${mutual[i].username}/profile`)}>
                        <img src={mutual[i].avatar} className="img-fluid" style={{borderRadius:'1rem'}}/>
                        <p style={{fontSize:'0.9rem'}}>{mutual[i].info.firstName}..</p>
                    </div>
                    </>
                )
            }
        }else{
            return(
                <>
                </>
            )
        }
    }

    return(
        <>
            <section className="user_profile pb-5">
                <div className="profile_bg" style={{backgroundImage:`url(${data && data.banner ? data.banner : 'https://i.pinimg.com/originals/23/4e/bd/234ebdb769596d09a2d2d3fc1bce8293.jpg'})`}}>
                    <div className="user_profile_pic" >
                        <img src={data && data.avatar ? data.avatar : 'https://therminic2018.eu/wp-content/uploads/2018/07/dummy-avatar.jpg'} className="img-fluid"/>
                    </div>
                </div>
                <div className="container py-3 mb-4">
                    <div className="row">
                        <div className="col-11 col-lg-8 mx-auto text-center">
                            {data && (<>
                                <p className="user_full_name">{data.info.firstName} {data.info.lastName}</p>
                                <p className="user_name">{data.username}</p>
                            </>)}  
                            {data.friends && data.friends.includes(user.id) ? (
                                <>
                                    <button className="friends_btn" onClick={()=>{handleUnfriend(data._id)}}><i className="fa fa-user"></i> Friends</button>
                                </>
                            ) : (
                                <>
                                    {mainUser && mainUser.requests.includes(data._id) ? (
                                        <>
                                            <button className="friends_btn" onClick={()=>handleAccept(data._id)}>{accpetLoading ? <LoadingOutlined/> : <><i className="fa fa-check"></i> Accept Request</>}</button>
                                            <button className="friends_btn" onClick={()=>handleReject(data._id)}>{rejectLoading ? <LoadingOutlined/> : <><i className="fa fa-close"></i> Reject Request</>}</button>
                                        </>
                                    ) : (
                                        <>
                                            {data.requests && data.requests.includes(user.id) ? (
                                                    <>
                                                        <button className="friends_btn" onClick={()=>removeFriendRequest(data._id)}><i className="fa fa-check"></i> Requested</button>
                                                    </>
                                                ) : (
                                                    <>
                                                        <button className="friends_btn" onClick={()=>sendRequest(data._id)}>{requestLoading ? <LoadingOutlined/> : <><i className="fa fa-user-plus"></i> Add Friend</>}</button>
                                                    </>
                                            )}
                                        </>
                                    )}
                                </>
                            )}
                            <p className="user_bio">{data && data.info.bio}</p>
                            {data && data.friends && data.friends.includes(user.id) && (<button className='user_profile_btn mt-4' onClick={()=>{setIsModalVisible(true)}}>Kuch Kahna Tha Kya?</button>)}
                            {data && (
                                <>
                                    <div className="mt-5 mb-3">
                                        <p className="text-left mutual_head">Friends</p>
                                        <p className="text-left mutual_sub_head"> {mutual.length} Mutual Friends</p>
                                        {mutual && mutual.length > 0 && 
                                        <p className="text-left mutual_modal_friend" onClick={()=>setIsMutualModalVisible(true)}>See All Mutual Friends</p>}
                                        <div className="row">
                                            {renderMutual()}
                                        </div>
                                    </div>
                                </>
                            )}
                            <MessageModal isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible} sendTo={data._id}/>      
                            <MutualModal isMutualModalVisible={isMutualModalVisible} mutual={mutual && mutual} setIsMutualModalVisible={setIsMutualModalVisible}/>
                        </div>
                    </div>
                </div>
            </section>
            <Navbar/>
        </>
    )
}

export default UserProfile