import React, {useState} from 'react'
import {Link, useHistory} from 'react-router-dom'
import './Feed.css'
import Navbar from './Navbar'
import {Tooltip} from 'antd'
import MessageInfo from './MessageInfo'
import { readMessage } from '../functions/message'
import {useSelector} from 'react-redux'

const Feed = ({heading, bgColor, iconBgColor, posts, handleClick,loadAllMessages, filled, sentTo, handleMessageDelete, handleGuessClick})=>{
    const history = useHistory()
    const [isInfoModalVisible, setIsInfoModalVisible] = useState(false)
    const [infoData, setInfoData] = useState('')
    const {user} = useSelector((state)=>({...state}))
    let mainColor, msgHead, msgBody;
    const btnBackColor = {
        liked:'linear-gradient(to right, #EB3349 0%, #F45C43  51%, #EB3349  100%)',
        msg:'linear-gradient(to right, #52c234 0%, #061700  51%, #52c234  100%)',
        send:'linear-gradient(to right, #00c6ff 0%, #0072ff  51%, #00c6ff  100%)'
    }

    if(sentTo){
        mainColor = btnBackColor.send
        msgHead = 'No Message Sent'
        msgBody = "Send secret messages to your friend without letting them know who has sent!!"
    }else if(filled){
        mainColor = btnBackColor.liked
        msgHead = 'No Liked Message'
        msgBody = "Messages liked by you can be find here..!!"
    }else{
        mainColor = btnBackColor.msg
        msgHead = 'No Message Yet'
        msgBody = "Looks like you haven't told your friends about your account.."
    }

    const handleInfoClick = (id)=>{
        setIsInfoModalVisible(true)
        readMessage(user && user.token, id).then((res)=>{
            setInfoData(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>  
            <section className="feed">
                <div className="heading" style={{background:bgColor}}>
                    <p>{heading}</p>
                </div>
                {posts && posts.length > 0 && (<i className="fa fa-refresh" onClick={loadAllMessages} style={{backgroundColor:iconBgColor}}></i>)}
                <div className="container pb-5 pt-5">
                    <div className="row">
                        {(posts && posts.length > 0) ? posts.map((item)=>{
                            return (
                                <>
                                    <div className="col-12 mx-auto mb-3" key={item._id}>
                                        <div className="card">
                                            <div className="card-body">
                                                <p className="title">{item.title}</p>
                                                <p className="description">{item.description.slice(0, 200)}...</p>
                                                {(item.guessed && !sentTo) ? (<><p>Sent By - {item && item.postedBy ? item.postedBy.username : 'Deactivated User'} </p></>) : (<></>)}
                                                {sentTo ? (
                                                    <>
                                                    <p className="sent_user">Sent to - {item && item.postedTo ?<Link to={`/user/${item.postedTo.username}/profile`}>{item.postedTo.username}</Link> : 'Deactivated User' }<br/>
                                                    </p>
                                                    </>
                                                    ) : (<></>)
                                                }
                                                {!sentTo ? (
                                                    <>
                                                        <Tooltip title={filled ? 'Unlike' : 'Like'}><i className={filled ? 'fa fa-heart filled' : 'fa fa-heart'} onClick={()=>handleClick(item._id)}></i></Tooltip>
                                                    </>) : (<></>)}

                                                {!item.guessed ? (<><Tooltip title={'Want To Guess?'}><i className='fa fa-question-circle' onClick={()=>handleGuessClick(item._id)}></i></Tooltip></>) : (<></>)}
                                                {sentTo ? (<><Tooltip title={'Delete'}><i className="fa fa-trash mr-3" onClick={()=>handleMessageDelete(item._id)}></i></Tooltip></>) : (<></>)}
                                                <Tooltip title="See Full Message"><i className="fa fa-info-circle" onClick={()=>handleInfoClick(item._id)}></i></Tooltip>
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )
                        }) : (<>
                        <div className="no_msg_cont">
                            <img src="https://illustratious.s3.amazonaws.com/screenshots/ee1zr3-No-message.png" className="img-fluid"/>
                            <p className="head">{msgHead}</p>
                            <p className="desc mb-3">{msgBody}</p>
                            <button className="btn-grad mx-auto" onClick={()=>history.push('/search')} style={{backgroundImage : mainColor}}>Search Your Friend..</button>
                        </div>
                        </>)}
                    </div>
                </div>
            </section>
            <MessageInfo isInfoModalVisible={isInfoModalVisible} infoData={infoData && infoData} setInfoData={setInfoData} setIsInfoModalVisible={setIsInfoModalVisible}/>
            <Navbar/>
            
        </>
    )
}

export default Feed