import React, {useState, useEffect} from 'react'
import {Modal} from 'antd'
import './MessageModal.css'
import {useSelector} from 'react-redux'
import { postMessage } from '../functions/message'
import GifModal from './GifModal'
import {toast} from 'react-toastify'
import { LoadingOutlined } from '@ant-design/icons'

const MessageModal = ({isModalVisible, setIsModalVisible, sendTo})=>{
    const [title, setTitle] = useState('')
    const [description, setDescription] = useState('')
    const [isGifVisible, setIsGifVisible] = useState(false)
    const [loading, setLoading] = useState(false)
    const {user} = useSelector((state)=>({...state}))
    const [gif, setGif] = useState('')
    const handleSubmit = ()=>{
        setLoading(true)
        postMessage(user && user.token, title, description, sendTo).then((res)=>{
            setLoading(false)
            setTitle('')
            setDescription('')
            setIsModalVisible(false)
        }).catch((err)=>{
            setLoading(false)
            toast.error('Some error occured...Make sure that title and description are not empty ant try again!!')
            console.log(err)
        })
    }
    useEffect(()=>{
        if(description.length === 150){
            setIsGifVisible(true)
            setGif('https://media1.tenor.com/images/7a43689624b7e82b03c47f3481bbeb6f/tenor.gif?itemid=19136770')
        }
    },[description])
    
    return(
        <>
            <Modal footer={false} maskClosable={false} visible={isModalVisible} title="Kahdo Joh Kahna Ho!!" onCancel={()=>{setIsModalVisible(false); setTitle(''); setDescription('')}}>
                <div className="container msg_form my-3">
                    <input type="text" value={title} onChange={(e)=>setTitle(e.target.value)} placeholder="Title"/><br/>
                    <textarea type="text" value={description} onChange={(e)=>setDescription(e.target.value)} placeholder="Description"></textarea>
                    <button className="btn-grad mx-auto mt-4" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
                    <GifModal isGifVisible={isGifVisible} setIsGifVisible={setIsGifVisible} gif={gif}/>
                </div>
            </Modal>
        </>
    )
}

export default MessageModal