import React, {useState ,useEffect} from 'react'
import Feed from '../../components/Feed'
import { getLikedMessages, removeFromLiked } from '../../functions/user'
import {useSelector} from 'react-redux'

const LikedMessages = ()=>{
    const [posts, setPosts] = useState('')
    const {user} = useSelector((state)=>({...state}))

    const loadAllMessages = ()=>{
        getLikedMessages(user && user.token).then((res)=>{
            setPosts(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    useEffect(()=>{
        loadAllMessages()
    },[user])

    const handleClick = (id)=>{
        removeFromLiked(user && user.token, id).then((res)=>{
            if(res.data.success){
                loadAllMessages()
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
            <Feed heading={'Liked Messages'} loadAllMessages={loadAllMessages} bgColor={'#ff0f3b'} iconBgColor={'#ff0f3b'} posts={posts && posts} handleClick={handleClick} filled={true}/>
        </>
    )
}

export default LikedMessages