import { Modal } from 'antd'
import React from 'react'
import {Link} from 'react-router-dom'
import './MessageInfo.css'

const MessageInfo = ({isInfoModalVisible, setIsInfoModalVisible, infoData, setInfoData})=>{
    let {guessed, description, title} = infoData && infoData
    return(
        <>
            <Modal visible={isInfoModalVisible} footer={false} centered onCancel={()=>{setInfoData('');setIsInfoModalVisible(false)}} title={title}>
                <p className="info_desc">{description}</p>
                {guessed && (<p>Sent By - <Link to={`/user/${infoData.postedBy.username}/profile`}>{infoData.postedBy.username}</Link></p>)}
            </Modal>

        </>
    )
}

export default MessageInfo