import axios from 'axios'

export const createOrUpdateUser = async(authtoken, email, info, username)=>{
    return await axios.post(`${process.env.REACT_APP_API}/create-or-update-user`, {email, username, info}, {
        headers:{
            authtoken
        }
    })
}

export const currentUser = async(authtoken)=>{
    return await axios.post(`${process.env.REACT_APP_API}/current-user`, {}, {
        headers:{
            authtoken
        }
    })
}

export const removeUser = async(authtoken)=>{
    return await axios.post(`${process.env.REACT_APP_API}/current/user/delete/remove`, {}, {
        headers:{
            authtoken
        }
    })
}

export const postReason = async(authtoken, reason)=>{
    return await axios.post(`${process.env.REACT_APP_API}/reason`, {reason}, {
        headers:{
            authtoken
        }
    })
}