import { LoadingOutlined } from '@ant-design/icons'
import { Modal } from 'antd'
import React,{useState} from 'react'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'
import { auth } from '../firebase'
import './ForgetPassModal.css'

const ForgetPassModal = ({isModalVisible, setIsModalVisible})=>{
    const {user} = useSelector((state)=>({...state}))
    const [email, setEmail] = useState(user && user.email)
    const [loading, setLoading] = useState(false)

    const handleSubmit = async()=>{
        setLoading(true)
        let config = {
            url:`https://kuch-kahna-tha.herokuapp.com/user/${user && user.username}`,
            handleCodeInApp:true
        }
        await auth.sendPasswordResetEmail(email, config).then((res)=>{
            setLoading(false)
            toast.success(`Password Reset Link is sent to ${email}...Please Check!!`)
        }).catch((err)=>{
            setLoading(false)
            toast.error('Some error occured...Please Try Again!!')
        })
    }

    const handleCancel = () =>{
        setIsModalVisible(false)
        setEmail(user && user.email)
    }
    return(
        <>  
            <Modal visible={isModalVisible} onCancel={handleCancel} title="Reset Password" footer={false}>
                <input type="text" placeholder="Email" className="forget_input" value={email ? email : ''} onChange={(e)=>setEmail(e.target.value)}/><br/>
                <button className="btn-grad mx-auto" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
            </Modal>
        </>
    )
}

export default ForgetPassModal