import { LoadingOutlined } from '@ant-design/icons'
import { Modal } from 'antd'
import React,{useState} from 'react'
import './GuessModal.css'

const GuessModal = ({isModalVisible,handleModalSubmit, name, setName, imgSrc,count, onModalClose, loading})=>{
    const handleChange = (e)=>{
        setName(e.target.value)
        document.querySelector('.guess_input').style.borderBottom = '0.1rem solid rgb(182, 182, 182)'
        document.querySelector('.error_msg').classList.add('d-none')
    }
    return(
        <>
            <Modal visible={isModalVisible} onCancel={onModalClose} footer={false} centered title='Guess The Name!'>
                <div className="guess_img text-center">
                    <img src={imgSrc} className={`img-fluid ${count == 0 ? 'auto' : ''}`}/>
                    <p>{count === 0 ? `Kaun Ho Skta hai Ye??` : ''}</p>
                </div>
                <input value={name} onChange={handleChange} className="guess_input mt-3" placeholder={'Enter the peson name..'} />
                <p className="error_msg text-danger d-none"><i className="fa fa-exclamation-circle pr-1"></i> <span className="error_msg_text"></span></p>
                <button className="btn-grad  mx-auto mt-3" onClick={handleModalSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
            </Modal>
        </>
    )
}

export default GuessModal