import React, {useState, useEffect} from 'react'
import { searchUser } from '../../functions/user'
import {useSelector, useDispatch} from 'react-redux'
import './Search.css'
import { Avatar } from '@material-ui/core'
import Navbar from '../../components/Navbar'

const Search = ({history})=>{
    const [text, setText] = useState('')
    const [users, setUsers] = useState([])
    const {user} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()
    useEffect(()=>{
        if(text.length !== 0){
            setTimeout(()=>{
                searchUser(user && user.token, text).then((res)=>{
                    setUsers(res.data)
                }).catch((err)=>{
                    console.log(err)
                })

            }, 400)
        }else{
            setUsers([])
        }
    },[text])

    const handleClick = (item)=>{
        if(item.email === user.email){
           history.push(`/user/${item.username}`)
           dispatch({
               type:'NAV_CHANGE',
               payload:'account'
           })
        }else{
            history.push(`/user/${item.username}/profile`)
        }
    }
    return(
        <>
           <section className="search container">
                <div className="my-3">
                    <input type="text" value={text} onChange={(e)=>{setText(e.target.value)}} placeholder="Search"/>
                </div>
                <div className="my-4">
                    {users && users.length > 0 ? (<>
                        {users.map((item)=>{
                            let {info, username, avatar} = item
                            return (
                                <>
                                    <div className="d-flex align-items-center mb-3" key={item._id} onClick={()=>handleClick(item)}>
                                        <Avatar src={avatar}
                                            style={{width:'4rem', height:'4rem', marginRight:'2rem', backgroundColor:'#d6d6d6'}}
                                        />
                                        <div className="search_user_info pt-3">
                                            <p>{username}</p>
                                            <p>{info.firstName} {info.lastName}</p>
                                        </div>
                                    </div>
                                    
                                </>
                            )
                        })}
                    </>) : (
                        <>
                        <div className="no_user_container">
                            <img src="https://i.pinimg.com/originals/15/f2/09/15f209bdae6da376665c3a1b2cb781ea.gif" className="img-fluid"/>
                            <p className="mt-5">Dhundne se toh bhagwaan mil jate hai....<br/>
                            Tum jise dhund rhe woh kaise nhi milega!! </p>
                        </div>
                        </>
                    )}
                </div>
           </section>
           <Navbar/>
        </>
    )
}
export default Search