import React, {useState} from 'react'
import { auth } from '../../firebase'
import { createOrUpdateUser } from '../../functions/auth'
import {useDispatch} from 'react-redux'
import './RegisterComplete.css'
import { LoadingOutlined } from '@ant-design/icons'

const RegisterComplete = ({history})=>{
    const initialState={
        firstName:'',
        lastName:'',
        dob:'',
        gender:''
    }
    const [email, setEmail] = useState(localStorage.getItem('emailForRegistration'))
    const [info, setInfo] = useState(initialState)
    const [username, setUsername] = useState('')
    const [password ,setPassword] = useState('')
    const [loading, setLoading] = useState(false)
    const dispatch = useDispatch()

    const handleInfoChange = (e)=>{
        setInfo({...info, [e.target.name] : e.target.value})
    }

    const handleSubmit=async()=>{
        setLoading(true)
        const result = await auth.signInWithEmailLink(email, window.location.href)
        if(result.user.emailVerified){
            localStorage.removeItem('emailForRegistration')
            const user = auth.currentUser
            await user.updatePassword(password)
            let idToken = await user.getIdTokenResult();
            let user_token = idToken.token
            createOrUpdateUser(user_token, email, info, username).then((res)=>{
                window.location.reload()
                setLoading(false)
                const {username, info, _id} = res.data
                dispatch({
                    id:_id,
                     email:res.data.email,
                    username,
                    info,
                    token:user_token
                })
            }).catch((err)=>{
                console.log(err)
                setLoading(false)
            })
        }
    }
    return(
        <>
        <section className="reg_comp">
            <div className="login_logo text-center mb-5">
                <p>Kuch Kahna Tha</p>
            </div>
            <div className="container my-5">
                <div className="row mx-auto">
                    <div className="col-12 col-lg-8 mx-auto text-center">
                        <input type='text' placeholder='First Name' value={info.firstName} name='firstName' onChange={handleInfoChange}/>
                        <input type='text' placeholder='Last Name' value={info.lastName} name='lastName' onChange={handleInfoChange}/>
                        <input type='text' placeholder='UserNAme' value={username} name='' onChange={(e)=>{setUsername(e.target.value)}}/>
                        <input type='text' value={email} disabled onChange={(e)=>setEmail(e.target.value)} placeholder="Email"/>
                        <input type='password' value={password} onChange={(e)=>setPassword(e.target.value)} placeholder="Password"/>
                        <input type='date' placeholder='Date Of Birth' value={info.dob} name='dob' onChange={handleInfoChange}/><br/>
                        <select name="gender" value={info.gender} onChange={handleInfoChange}>
                            <option>Gender</option>
                            <option value="Male">Male</option>
                            <option value="Female">Female</option>
                        </select>
                        <button className="btn btn-primary mt-3 mb-3" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Submit'}</button>
                    </div>
                </div>
            </div>
        </section>
            
        </>
    )
}

export default RegisterComplete