import Avatar from 'antd/lib/avatar/avatar'
import Modal from 'antd/lib/modal/Modal'
import React from 'react'

const MutualModal = ({isMutualModalVisible, setIsMutualModalVisible, mutual})=>{
    return(
        <>
        <Modal visible={isMutualModalVisible} footer={false} onCancel={()=>setIsMutualModalVisible(false)} title={`${mutual && mutual.length} Mutual Friends`}>
        <section className="friend_request">
                <div className="pt-3">
                    <div className="row">
                        {mutual && mutual.length > 0 ? mutual.map((item)=>{
                            return (
                                <>
                                    <div className="col-12 mx-auto friend_card mb-4 d-flex justify-content-between align-items-center">
                                        <Avatar src={item.avatar} style={{height:'80px', width:'80px'}}/>
                                        <div className="text-right">
                                            <p>{item.info.firstName} {item.info.lastName}</p>
                                            <p>{item.username}</p>
                                        </div>   
                                    </div>
                                </>
                            )
                        }) : (
                            <>
                            </>
                        )}
                        
                    </div>
                </div>

            </section>
        </Modal>
        </>
    )
}

export default MutualModal