import { LoadingOutlined } from '@ant-design/icons'
import { Modal } from 'antd'
import React from 'react'
import {useSelector} from 'react-redux'

const DeleteAccount = ({isDeleteModalVisible, setisDeleteModalVisible, handleDeleteAccount,loading,reason, setReason})=>{
    const {user} = useSelector((state)=>({...state}))
    const paraStyle = {
        fontSize:'1rem',
        letterSpacing:'1px',
        fontWeight:'500'
    }
    const deleteButtonStyle = {
        border:'0.1rem solid black',
        backgroundColor:'black',
        color:'white',
        width:'40%',
        marginRight:'10px',
        padding:'7px 15px',
        fontSize:'1rem',
        letterSpacing:'1px',
        textTransform:'uppercase',
        borderRadius:'1.3rem'
    }
    const cancelButtonStyle = {
        border:'0.1rem solid black',
        backgroundColor:'white',
        color:'black',
        width:'40%',
        marginRight:'10px',
        padding:'7px 15px',
        fontSize:'1rem',
        letterSpacing:'1px',
        textTransform:'uppercase',
        borderRadius:'1.3rem'
    }

    return(
        <>
        <Modal visible={isDeleteModalVisible} onCancel={()=>setisDeleteModalVisible(false)} footer={false}>
            <img src="https://cutewallpaper.org/21/sad-animated-images/Pin-on-Gif.gif" className="img-fluid" style={{height:'200px', width:'200px', margin:'0 auto', display:'block'}}/>
            <div className="text-center mt-3">
                <p style={paraStyle}>Tum bhi jaa {user && user.info.gender === 'Male' ? 'rhe' : 'rhi'} ho chhodh ke...Woh {user && user.info.gender === 'Male' ? 'gya tha' : 'gayi thi'} bina reason bataye.. kam se kam tum reason bata kr jana..!!</p>
                <input className="reason_input mb-4" placeholder="Reason" type="text" value={reason} onChange={(e)=>setReason(e.target.value)}/><br/>
                <button style={deleteButtonStyle} onClick={handleDeleteAccount}>{loading ? <LoadingOutlined/> : 'Delete'}</button>
                <button style={cancelButtonStyle} onClick={()=>setisDeleteModalVisible(false)}>Cancel</button>
            </div>
        </Modal>
        </>
    )
}

export default DeleteAccount