import axios from 'axios'

export const getMessage = async(authtoken)=>{
    return await axios.get(`${process.env.REACT_APP_API}/messages`, {
        headers:{
            authtoken
        }
    })
}

export const readMessage = async(authtoken, id)=>{
    return await axios.get(`${process.env.REACT_APP_API}/message/${id}`, {
        headers:{authtoken}
    })
}

export const getUserMessage = async(authtoken)=>{
    return await axios.get(`${process.env.REACT_APP_API}/user/messages`, {
        headers:{
            authtoken
        }
    })
}

export const getMessagePostedTo = async(authtoken, postedTo)=>{
    return await axios.post(`${process.env.REACT_APP_API}/user/message/posted`, {postedTo}, {
        headers:{
            authtoken
        }
    })
}

export const postMessage = async(authtoken, title, description, postedTo)=>{
    return await axios.post(`${process.env.REACT_APP_API}/message`, {title, description, postedTo}, {
        headers:{
            authtoken
        }
    })
}

export const removeMessage = async(authtoken, id)=>{
    return await axios.delete(`${process.env.REACT_APP_API}/message/${id}`, {
        headers:{
            authtoken
        }
    })
}

export const updateGuess = async(authtoken, id, guessed)=>{
    return await axios.put(`${process.env.REACT_APP_API}/message/${id}`, {guessed}, {
        headers:{authtoken}
    })
}