import { Modal } from 'antd'
import React from 'react'

const GifModal = ({isGifVisible, setIsGifVisible, gif, handleLogout, logout, setGif})=>{
    return(
        <>
        <section className="gif_modal">
            <Modal visible={isGifVisible} footer={false} closeIcon={false} centered onCancel={()=>{setIsGifVisible(false)}}>
                <img src={gif} className="img-fluid"/>
                {logout ? (<>
                    <hr/>
                    <button className="btn btn-sm px-4 btn-primary" onClick={handleLogout}>Logout</button>
                    <button className="btn btn-sm btn-outline-primary px-4 ml-3" onClick={()=>{setIsGifVisible(false)}}>Don't Logout</button>
                </>) : (<></>)}
            </Modal>
        </section>
        </>
    )
}

export default GifModal