import React, {useState} from 'react'
import {useHistory} from 'react-router-dom'
import './Navbar.css'
import {useSelector, useDispatch} from 'react-redux'
import {BottomNavigation, BottomNavigationAction} from '@material-ui/core'
import {Home, Favorite, Send, AccountCircle, SearchOutlined} from '@material-ui/icons'

const Navbar = ()=>{
    const {user, nav} = useSelector((state)=>({...state}))
    const [value, setValue] = useState(nav)
    const dispatch = useDispatch()
    const history = useHistory()

    const handleChange = (e, val)=>{
        setValue(val)
        dispatch({
            type:'NAV_CHANGE',
            payload:val
        })
    }

    return(
        <>
            <BottomNavigation value={value} onChange={handleChange} style={{width:'100%', position:'fixed', bottom:'0', boxShadow:'0px 3px 6px rgba(0, 0, 0, 0.5)'}}>
                <BottomNavigationAction label="Home" value="home" icon={<Home />} onClick={()=>history.push(`/messages`)}/>
                <BottomNavigationAction label="Favorites" value="favorites" icon={<Favorite />} onClick={()=>history.push(`/messages/liked`)}/>
                <BottomNavigationAction label='Search' value='Search' icon={<SearchOutlined/>} onClick={()=>history.push('/search')}/>
                <BottomNavigationAction label="Send" value="Send" icon={<Send />} onClick={()=>history.push(`/messages/send`)}/>
                <BottomNavigationAction label={`${user && user.username}`} value="account" icon={<AccountCircle />} onClick={()=>history.push(`/user/${user.username}`)} />
            </BottomNavigation>
        </>
    )
}

export default Navbar