import React from 'react'
import {Modal} from 'antd'

const BannerModal = ({isBannerModalVisible, setIsBannerModalVisble, banners, handleBannerChange})=>{

    return(
        <>
        <Modal visible={isBannerModalVisible} footer={false} centered onCancel={()=>setIsBannerModalVisble(false)} title={'Banners!!'}>
            <div className="row">
                {banners && banners.map((item)=>{
                    return (
                        <>
                            <div className="col-6 col-lg-4 mb-4">
                                <img src={item} className="img-fluid" onClick={()=>handleBannerChange(item)}/>
                            </div>
                        </>
                    )
                })}
            </div>
        </Modal>
        </>
    )
}

export default BannerModal