import { Drawer } from 'antd'
import React, {useState, useEffect} from 'react'
import './SideNav.css'
import {useHistory} from 'react-router-dom'
import {useSelector, useDispatch} from 'react-redux'
import ForgetPassModal from './ForgetPassModal'
import { logoutImages } from '../pages/universal/images'
import { getAllFriends, getRequests } from '../functions/user'
import firebase from 'firebase'
import { postReason, removeUser } from '../functions/auth'
import DeleteAccount from './DeleteAccount'
import { toast } from 'react-toastify'

const SideNav = ({isDrawerVisible, setIsDrawerVisible, username, setIsGifVisible,setGif})=>{
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [isDeleteModalVisible, setisDeleteModalVisible] = useState(false)
    const {user} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()
    const [request, setRequests] = useState(0)
    const [friends, setFriends] = useState(0)
    const [reason, setReason] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()

    const loadRequests = ()=>{
        getRequests(user && user.token).then((res)=>{
            setRequests(res.data.length)
        }).catch((err)=>{
            console.log(err)
        })
    }

    
    const loadAllFriends = ()=>{
        getAllFriends(user && user.token).then((res)=>{
            setFriends(res.data.length)
        }).catch((err)=>{
            console.log(err)
        })
    }

    useEffect(()=>{
        loadRequests()
        loadAllFriends()
    }, [user])

    const handleDeleteAccount = async() =>{
        setLoading(true)
        if(reason){
            postReason(user && user.token, reason).then(async(res)=>{
                if(res.data.received){
                    const activeUser = firebase.auth().currentUser
                    await activeUser.delete().then((result)=>{
                        if(result === undefined){
                            removeUser(user && user.token).then((x)=>{
                                setLoading(false)
                                setReason('')
                                setisDeleteModalVisible(false)
                                dispatch({
                                    type:'LOG_OUT_USER',
                                    payload:null
                                })
                                history.push('/login')
                            })
                        }
                    }).catch((err)=>{
                        setLoading(false)
                        console.log(err)
                    })
                }
            }).catch((err)=>{
                setLoading(false)
                console.log(err)
            })
        }else{
            toast.error('Please write the reason!!')
            setLoading(false)
        }
    }

    return(
        <>
        <Drawer visible={isDrawerVisible} onClose={()=>setIsDrawerVisible(false)} mask={false} placement="right" title={user && user.username}>
            <div className="item mt-2" onClick={()=>history.push(`/user/${username}/edit`)}>
                <p><i className="fa fa-edit mr-4"></i>Edit Profile</p>
            </div>
            <div className="item mt-2" onClick={()=>setIsModalVisible(true)}>
                <p><i className="fa fa-home mr-4"></i>Change Password</p>
            </div>
            <div className="item mt-2" onClick={()=>setisDeleteModalVisible(true)}>
                <p><i className="fa fa-trash mr-4"></i>Delete Your Account</p>
            </div>
            <div className="item mt-2" onClick={()=>history.push('/friend/requests')}>
                <p><i className="fa fa-user mr-4" ></i>Friend Requests <span className='badge badge-primary'>{request && request}</span></p>
            </div>
            <div className="item mt-2" onClick={()=>history.push('/user/friends/all')}>
                <p><i className="fa fa-users mr-4" ></i>Friends <span className='badge badge-primary'>{friends && friends}</span></p>
            </div>
            <hr/>
            <div className="item" onClick={()=>{setIsGifVisible(true); setGif(logoutImages[Math.floor((Math.random() * logoutImages.length))])}}>
                <p><i className="fa fa-sign-out mr-4"></i>Logout</p>
            </div>
            <hr/>
        </Drawer>
        <ForgetPassModal isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible}/>
        <DeleteAccount reason={reason} loading={loading} setReason={setReason} isDeleteModalVisible={isDeleteModalVisible} setisDeleteModalVisible={setisDeleteModalVisible} handleDeleteAccount={handleDeleteAccount}/>
        </>
    )
}

export default SideNav